import React, { Component } from 'react';
import './App.css'

const keywords = ['Mathematics', 'Physics', 'Chemistry', 'Biology', 'Computers', 'Science & Technology']

class Keyword extends Component{
    constructor(props){
        super(props);
        this.state = {
            keyword: 'RESEARCH',
            keywordArray : [],
            visibilityOff : 'hidden',
            visibilityOn : 'visible',
        }
    }


    nextKeyword =() =>{
        let i= this.state.keywordArray.length;
        if(this.state.keywordArray.length === keywords.length){
        }else{
            this.setState({
                keyword: keywords[i].toUpperCase(),
                keywordArray: [...this.state.keywordArray, keywords[i]]
            })
        }
        // console.log('keyword')
        this.render()
    }

    updateHeading =() =>{
        this.setState({
            visibilityOff : 'visible',
            visibilityOn : 'hidden',
        })
    }
    componentDidMount(){
        // console.log('Will check condition now')
        this.checkKeyword()
        // if(this.state.keywordArray.length !== keywords.length){
            // setInterval(this.checkKeyword, 1000);
        //     // console.log('true');
        //     // return false;
        // }
    }

    switchInterval =() =>{
        let interval = 1000

        switch(this.state.keywordArray.length){
            case 0 : interval = 1000;
                        break;
            default : interval = 1000;
                        break;
        }
        // console.log(interval)
        return interval;
        
    }
    checkKeyword =() =>{
        // if(this.state.keywordArray.length !== keywords.length){
        //     setInterval(this.nextKeyword, 1000);
        // let arrayLength = this.state.keywordArray.length
        // let constArray = keywords.length

        // console.log(arrayLength +' - '+ constArray)
        if(this.state.keywordArray.length === 0){
            // setTimeout(this.nextKeyword, 0);
            setInterval(this.nextKeyword, this.switchInterval());
            console.log((keywords.length+2)*1000)
            setTimeout(this.updateHeading, ((keywords.length+2)*1000))
            // setTimeout(this.updateHeading, ((keywords.length+4)*1000))
            // return true

        }else if(this.state.keywordArray.length !== keywords.length){
            // setTimeout(this.nextKeyword, 1000);
            setInterval(this.nextKeyword, 1000);
            // console.log('true');
            // return true;
        }else{
            return false
        }
    }
    
    render(){

        // console.log('rendered')
        return(
            // <div>
            //     {console.log('Rendered div')}
            //     {/* <p>Crazy about {this.state.keyword}</p> */}
            //     {/* <p>Crazy about {(this.checkKeyword)? this.state.keyword : 'IRM.'}</p> */}
            //     {(this.checkKeyword)? this.state.keyword : 'IRM.'}
            // </div>
            // (this.checkKeyword)? this.state.keyword : 'IRM.'

            <div id='crazy'>
                <p className={this.state.visibilityOn}>Crazy about <span>{(this.checkKeyword)? this.state.keyword : 'IRM.'}</span></p>   
                <p className={this.state.visibilityOff}>WORK with <span>IRM.</span></p>
                <p className={`${this.state.visibilityOff} smallGreen`}>Institute of RESEARCH MANIACS</p>
            </div>
        )
    }

}

class App extends Component{
    render(){
        return(
            // <div id='crazy'>
            //     <p class={this.state.visibilityOn}>Crazy about <span><Keyword /></span></p>   
            //     <p class={this.state.visibilityOff}>work with <span>IRM.</span></p>
            // </div>

            <Keyword />
        )
    }
}

export default App;